.sidebarRow{
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}

/* on hover, background color goes lightgray with border rad*/
.sidebarRow:hover{
        background-color: lightgray;
        border-radius: 10px;
}

.sidebarRow > h4{
    margin-left: 20px;
    font-weight: 600;
}

.sidebarRow > .MuiSvgIcon-root{
    font-size: xx-large;
    color: #2e81f4;
}
.feed{
    flex: 1 1;
    padding: 30px 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.storyReel{
    display: flex;
}
.story{
    position: relative;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 120px;
    height: 200px;
    box-shadow: 0px 5px 17px -7px rgba(0,0,0,0.75);
    border-radius: 10px;
    margin-right: 10px;
    transition: -webkit-transform 100ms ease-in;
    transition: transform 100ms ease-in;
    transition: transform 100ms ease-in, -webkit-transform 100ms ease-in;
    cursor: pointer;
}

.story:hover{
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
}

.story__avatar{
    margin: 10px;
    border: 5px solid #2e81f4;
}

.story > h4{
    position: absolute;
    bottom: 20px;
    left: 20px;
    color: white;
}
.messageSender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0,0,0,0.75);
    width: 100%;
}

.messageSender__top{
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding: 15px;
}

.messageSender__top > form{
    flex: 1 1;
    display: flex;
    
}

.messageSender__top > form > input{
    outline-width: 0;
    border: none;
    padding: 5px 20px;
    margin: 0 10px;
    border-radius: 999px;
    background-color: #eff2f5;
}

.messageSender__top > form > button{
    display: none;
}

.messageSender__input{
    flex: 1 1;
}

.messageSender__bottom{
    display: flex;
    justify-content: space-evenly;
}

.messageSender__option{
    padding: 20px;
    display: flex;
    align-items: center;
    color: gray;
    margin: 5px;
}

.messageSender__option > h3{
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}

.messageSender__option:hover{
    background-color: #eff2f5;
    border-radius: 20px;
}
.post{
    width: 100%;
    margin-top: 15px;
    border-radius: 15px;
    background-color: white;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
    width: 100%;
}

.post__top{
    display: flex;
    position: relative;
    align-items: center;
    padding: 15px;
}

.post__avatar{
    margin-right: 10px;
}

.post__topInfo > h3{
    font-size: medium;
}

.post__topInfo > p{
    font-size: small;
    color: gray;
}
.post__bottom{
    margin-top: 0px;
    margin-bottom: 0px;
    padding-top: 0%;
    padding-right: 15px;
    padding-left: 15px;
    padding-bottom: 15px;
}

.post__image > img{
    width: 100%;
}

.post__options{
    padding-top: 10px;
    border-top: 1px solid lightgray;
    display: flex;
    justify-content: space-evenly;
    font-size: medium;
    color: gray;
    cursor: pointer;
    padding: 15px;
}

.post__option{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    flex: 1 1;
}

.post__option > p{
    margin-left: 10px;
}

.post__options :hover{
    background-color: #eff2f5;
    border-radius: 5px;
}
.widgets {
    padding: 35px 10px;
    flex: 1 1;
    padding-right: 10px;
}

.details{
    display: flex;
    align-items: center;
    padding-bottom: 10px;

}

.details__icons{
    display: flex;
    align-items: center;

}

.details > h4{
    font-size: 18px;
    font-weight: 650;
    line-height: 118%;
    letter-spacing: -0.4px;
    color: rgba(101, 103, 107, 1);
    width: 100%;
}

.details__icons > .MuiSvgIcon-root{
    font-size: x-large;
    cursor: pointer;
    margin-left: 10px;
}

