.contacts{
    display: flex;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-right: 10px;
    cursor: pointer;
    margin-right: 10px;

}

.avatar{
    height: 35px !important;
    width: 35px !important;
}

.contacts > h4 {
    font-weight: 600;
    margin-left: 10px;
} 
.login {
    display: grid;
    place-items: center;
    height: 100vh;
}

.login > button{
    width: 300px;
    background-color: #2e81f4;
    color: #eff2f5;
    font-weight: 600;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.login > button:hover {
    background-color: #303030;
    color: #2e81f4;
}

.login > div > img:first-child {
    object-fit: contain;
    height: 150px;
}

.login > div > img:last-child {
    object-fit: contain;
    height: 50px;
    padding-top: 25px;
}

.login > div{
    display: flex;
    flex-direction: column;
}
